import aboutMeImg from "../images/aboutme.jpg";
import SocialIcons from "../components/SocialIcons";
import { useState } from "react";
import resume from "../pages/about/Kunal_Kalpande_Resume.pdf";

const AboutMe = ({ name, email, brand }) => {
  const [downloading, setDownloading] = useState(false);

  const handleDownload = () => {
    setDownloading(true);
    const link = document.createElement("a");
    link.href = resume;
    link.download = "Kunal_Kalpande_Resume.pdf";
    window.open(`${link.href}`);
    link.onload = () => {
      link.remove();
      setDownloading(false);
    };
    document.body.appendChild(link);
    link.click();
  };

  return (
    <div className="aboutContainer container">
      <div className="row">
        <img className="home-img" src={aboutMeImg} alt={name} />
        <div className="contentContainer">
          <h4>Nice to meet you</h4>
          <h5 id="user-detail-intro">
            I am a result-oriented MERN Stack Developer and always ardent about
            developing useful and amazing web applications. I have a strong
            foundation in both Frontend and Backend technologies and have strong
            problem-solving skills.
          </h5>
          <div className="contentDescription">
            <p>{brand}</p>
          </div>
          <div className="infoContainer">
            <div className="row">
              <div className="col-12 col-md-6 info">
                <span>Name:</span>
                <p>Kunal Kalpande</p>
              </div>
              <div className="col-12 col-md-6 info">
                <span>Phone:</span>
                <p id="contact-phone">+91 7057504420</p>
              </div>
              <div className="col-12 col-md-6 info">
                <span>Email:</span>
                <p>
                  <a id="contact-email" href={`kalpandekunal007@gmail.com`}>
                  kalpandekunal007@gmail.com
                  </a>
                </p>
              </div>
            </div>
            <div className="row">
              <div className="col-12 col-md-6 info">
                <span>Location:</span>
                <p>Akola, Maharastra</p>
              </div>
            </div>
          </div>
          <div className="buttonContainer">
            <a href=""
              className="btn downloadCV"
              id="resume-button-3"
              onClick={handleDownload}
              disabled={downloading}
            >
              {downloading ? "Downloading..." : "Download Resume"}
            </a>{" "}
            <SocialIcons />
          </div>
        </div>
        {/* </motion.div> */}
      </div>
    </div>
  );
};

export default AboutMe;
